@import url(https://fonts.googleapis.com/css?family=Marck+Script&subset=latin,latin-ext,cyrillic);
/*wariables*/
/*mixins*/
.text-red {
  color: red;
}
.tooltip-inner {
  color: red;
  background-color: #fff;
}
.tooltip.top .tooltip-arrow {
  border-top-color: #fff;
}
.tooltip.bottom .tooltip-arrow {
  border-bottom-color: #fff;
}
.tooltip.left .tooltip-arrow {
  border-left-color: #fff;
}
.tooltip.right .tooltip-arrow {
  border-right-color: #fff;
}
.mtop-15 {
  margin-top: 15px;
}
.img-responsive.img-responsive-100 {
  width: 100%;
}
/*desktop styles*/
/*sticky footer*/
html,
body {
  height: 100%;
  background: black;
  color: white;
  background-image: url(../img/bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
html .sticky-footer-wraper,
body .sticky-footer-wraper {
  min-height: 100%;
  margin-bottom: -100px;
  /* equal to footer height */
}
html .sticky-footer-wraper:after,
body .sticky-footer-wraper:after {
  content: "";
  display: block;
}
html .sticky-footer,
body .sticky-footer,
html .sticky-footer-wraper:after,
body .sticky-footer-wraper:after {
  height: 100px;
}
/*sticky footer end*/
/*load animation*/
.loadanimation {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 11000;
}
.loadanimation .spinner {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 50px;
  height: 50px;
}
.loadanimation .spinner .fa {
  font-size: 50px;
}
/*popup*/
.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  border: 1px solid red;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  border-radius: 0px;
  z-index: 10000;
  overflow: auto;
}
.popup.popup-small {
  background: rgba(0, 0, 0, 0.95);
  overflow: hidden;
  border: none;
}
.popup.popup-small .popup-child {
  margin-top: 1%;
}
.popup .popup-child .close-button {
  position: fixed;
  margin: auto;
  top: 10px;
  right: 20px;
  width: 45px;
  height: 45px;
  padding: 9px 10px;
  font-size: 25px;
  cursor: pointer;
  z-index: 10999;
}
.popup .popup-child .close-button:hover {
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
  transform: scale(1.05);
}
.popup .popup-child .project-description {
  margin-top: 20px;
}
.popup .popup-child .project-description #gallery {
  overflow: hidden;
}
.popup .popup-child .project-description #gallery .img-responsive {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}
.popup .popup-child .project-description #gallery .img-responsive.active {
  opacity: 100;
}
.popup .popup-child .project-description #gallery .control {
  position: absolute;
  margin: auto;
  padding-left: 5px;
  width: 50px;
  height: 50px;
  top: 0;
  bottom: 0;
  font-size: 40px;
  cursor: pointer;
  z-index: 1099;
  border-radius: 50px;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup .popup-child .project-description #gallery .control.next {
  right: 15px;
}
.popup .popup-child .project-description #gallery .control.prev {
  left: 15px;
}
.popup .popup-child .project-description .project-description-item {
  font-size: 2em;
}
.popup .popup-child .project-description .project-description-item .fa,
.popup .popup-child .project-description .project-description-item .arricon {
  color: red;
}
.popup .popup-child .project-description .project-description-item a {
  color: red;
  -webkit-transition: all 0.2s ease-out 0.2s;
  -moz-transition: all 0.2s ease-out 0.2s;
  -o-transition: all 0.2s ease-out 0.2s;
  transition: all 0.2s ease-out 0.2s;
}
.popup .popup-child .project-description .project-description-item a:hover {
  color: white;
  text-decoration: none;
}
.popup .popup-child .project-video {
  margin-bottom: 20px;
}
.popup .popup-child .project-video .video .play {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup .popup-child .project-video .video .play .btn-play {
  display: block;
  margin: 25% auto;
}
.popup .popup-child .project-video .video .play .btn-play:focus {
  outline: none;
}
/*popup end*/
/*popup 2*/
.form-black .form-control {
  color: white;
  border-color: red;
  background-color: transparent;
}
.form-black .has-error .form-control {
  border-color: #ffff00;
}
.form-black .has-error .help-block {
  color: #ffff00;
  padding-left: 15px;
}
.form-black .has-error .control-label {
  color: white;
}
.form-black .alert-success {
  background: transparent;
}
.form-black .alert-danger {
  background: transparent;
  border-color: #ffff00;
  color: #ffff00;
}
/*popup 2 end*/
.main-title {
  font-family: 'Marck Script', cursive;
  font-size: 5em;
  color: red;
}
.hr-top {
  width: 80%;
  height: 1px;
  background: -moz-linear-gradient(left, rgba(191, 11, 11, 0) 0%, rgba(191, 11, 11, 0.68) 10%, #bf0b0b 50%, rgba(191, 11, 11, 0.68) 90%, rgba(191, 11, 11, 0) 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(191, 11, 11, 0)), color-stop(10%, rgba(191, 11, 11, 0.68)), color-stop(50%, #bf0b0b), color-stop(90%, rgba(191, 11, 11, 0.68)), color-stop(100%, rgba(191, 11, 11, 0)));
  background: -webkit-linear-gradient(left, rgba(191, 11, 11, 0) 0%, rgba(191, 11, 11, 0.68) 10%, #bf0b0b 50%, rgba(191, 11, 11, 0.68) 90%, rgba(191, 11, 11, 0) 100%);
  background: -o-linear-gradient(left, rgba(191, 11, 11, 0) 0%, rgba(191, 11, 11, 0.68) 10%, #bf0b0b 50%, rgba(191, 11, 11, 0.68) 90%, rgba(191, 11, 11, 0) 100%);
  background: -ms-linear-gradient(left, rgba(191, 11, 11, 0) 0%, rgba(191, 11, 11, 0.68) 10%, #bf0b0b 50%, rgba(191, 11, 11, 0.68) 90%, rgba(191, 11, 11, 0) 100%);
  background: linear-gradient(to right, rgba(191, 11, 11, 0) 0%, rgba(191, 11, 11, 0.68) 10%, #bf0b0b 50%, rgba(191, 11, 11, 0.68) 90%, rgba(191, 11, 11, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00bf0b0b', endColorstr='#00bf0b0b', GradientType=1);
  margin: 0 auto;
}
.btn-transparent {
  color: red;
  border-color: red;
  background-color: transparent;
  -webkit-transition: all 0.2s ease-out 0.2s;
  -moz-transition: all 0.2s ease-out 0.2s;
  -o-transition: all 0.2s ease-out 0.2s;
  transition: all 0.2s ease-out 0.2s;
}
.btn-transparent.btn-abs {
  position: absolute;
  top: 30px;
  right: 30px;
}
.btn-transparent:hover {
  color: white;
  border-color: white;
  outline: none;
}
.btn-transparent:active,
.btn-transparent:focus {
  color: white;
  outline: none;
}
.lang-selector {
  position: absolute;
  margin: 15px 0 0 15px;
}
.lang-selector a {
  padding: 10px;
  color: red;
}
.lang-selector a.active {
  color: white;
  text-decoration: underline;
}
.content {
  margin-top: 50px;
}
.content .carousel {
  min-height: 28vw;
  /*.mtop(@carouselItemSize/2);*/
  position: relative;
  padding: 1px;
  overflow: hidden;
}
.content .carousel .control {
  position: absolute;
  margin: auto;
  width: 50px;
  height: 50px;
  top: 0;
  bottom: 0;
  font-size: 50px;
  cursor: pointer;
  z-index: 1099;
}
.content .carousel .control.next {
  right: 15px;
}
.content .carousel .control.prev {
  left: 15px;
}
.content .carousel .carousel-item {
  position: absolute;
  margin: auto;
  width: 14vw;
  height: 14vw;
  top: 0;
  bottom: 0;
  background: red;
  z-index: 1;
  cursor: pointer;
}
.content .carousel .carousel-item img {
  width: 100%;
  height: auto;
}
.content .carousel .carousel-item .project-title {
  position: absolute;
  bottom: 25px;
  left: 0;
  padding: 15px;
  background-color: rgba(255, 0, 0, 0.3);
  color: white;
}
.content .carousel .carousel-item .project-title h3 {
  margin: 0;
  font-size: inherit;
}
.content .carousel .carousel-item:nth-child(2) {
  width: 24.5vw;
  height: 24.5vw;
  left: 0;
  right: 0;
  z-index: 2;
  font-size: 1.9em;
}
.content .carousel .carousel-item:nth-child(2n+1) {
  /*background: lighten(@colorHeading, 30%);*/
  right: 30%;
  font-size: 1em;
}
.content .carousel .carousel-item:first-child {
  right: auto;
  left: 30%;
  font-size: 1em;
}
.content .carousel .carousel-item:nth-child(n+5) {
  display: none;
}
.content .carousel .carousel-item:last-child,
.content .carousel .carousel-item:nth-child(4) {
  display: block;
  width: 8vw;
  height: 8vw;
  left: 0;
  right: 0;
  z-index: 1;
  font-size: 0.57142857em;
}
.sticky-footer .arricon,
.sticky-footer .fa {
  font-size: 2em;
  color: red;
}
.modal-content {
  background-color: black;
  color: white;
}
.loadanimation {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.95);
  z-index: 11000;
}
.loadanimation .spinner {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 50px;
  height: 50px;
}
.loadanimation .spinner .fa {
  font-size: 50px;
  color: red;
}
.img-popover {
  position: fixed;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: auto;
  z-index: 10000;
}
/*mobile styles*/
@media screen and (max-width: 767px) {
  .img-popover {
    display: none;
  }
  p,
  .btn,
  input,
  div,
  span,
  h4 {
    font-size: 100%;
  }
  h1 {
    font-size: 20px;
  }
  h2 {
    font-size: 18px;
  }
  h3 {
    font-size: 16px;
  }
  .margin-fix {
    margin-bottom: 15px;
  }
  .sticky-footer .text-right {
    text-align: left;
  }
  .sticky-footer .copyright.pull-left {
    float: none !important;
  }
  .btn-transparent.btn-abs {
    position: absolute;
    top: 0px;
    right: 0px;
  }
  .lang-selector {
    margin: 7px 0 0 0;
  }
  .popup.popup-small .popup-child .main-title {
    margin-top: 10px;
    font-size: 2.2em;
  }
  .popup.popup-small .popup-child label {
    display: none;
  }
  .popup.popup-small .popup-child .help-block {
    font-size: .8em;
    margin-bottom: 0;
  }
}
@media screen and (max-width: 767px) and (orientation: portrait) {
  .content {
    /*.mtop(50px)*/
  }
  .content .carousel {
    min-height: 100vw;
    /*.mtop(@carouselItemSize/2);*/
    position: relative;
    padding: 1px;
  }
  .content .carousel .control {
    font-size: 40px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 0px 4px;
  }
  .content .carousel .control.next {
    right: 5px;
  }
  .content .carousel .control.prev {
    left: 5px;
  }
  .content .carousel .carousel-item {
    width: 50vw;
    height: 50vw;
  }
  .content .carousel .carousel-item:nth-child(2) {
    width: 87.5vw;
    height: 87.5vw;
    left: 0;
    right: 0;
    z-index: 2;
  }
  .content .carousel .carousel-item:nth-child(2n+1) {
    /*background: lighten(@colorHeading, 30%);*/
    right: 7%;
  }
  .content .carousel .carousel-item:first-child {
    right: auto;
    left: 7%;
  }
  .content .carousel .carousel-item:nth-child(n+5) {
    display: none;
  }
  .content .carousel .carousel-item:last-child,
  .content .carousel .carousel-item:nth-child(4) {
    display: block;
    width: 28.57142857vw;
    height: 28.57142857vw;
    left: 0;
    right: 0;
    z-index: 1;
  }
}
@media screen and (max-width: 767px) and (orientation: landscape) {
  .content {
    /*.mtop(50px)*/
  }
  .content .carousel {
    min-height: 50vw;
  }
  .content .carousel .control.next {
    right: 15px;
  }
  .content .carousel .control.prev {
    left: 15px;
  }
  .content .carousel .carousel-item {
    width: 25vw;
    height: 25vw;
  }
  .content .carousel .carousel-item:nth-child(2) {
    width: 43.75vw;
    height: 43.75vw;
    z-index: 2;
  }
  .content .carousel .carousel-item:nth-child(2n+1) {
    /*background: lighten(@colorHeading, 30%);*/
    right: 20%;
  }
  .content .carousel .carousel-item:first-child {
    right: auto;
    left: 20%;
  }
  .content .carousel .carousel-item:nth-child(n+5) {
    display: none;
  }
  .content .carousel .carousel-item:last-child,
  .content .carousel .carousel-item:nth-child(4) {
    display: block;
    width: 14.28571429vw;
    height: 14.28571429vw;
    z-index: 0;
  }
  .popup.popup-small {
    overflow-y: auto;
  }
}
